<template>
    <div class="home">
      <div class="swiper-container">
        <div class="swiper-wrapper">
          <div class="swiper-slide" @click="$router.push({ path:'/xiangqin',query:{id:item.id}})" v-for="item in banners" :key="item.id" >
            <img :src="item.banner" alt="">
          </div>
        </div>
        <div class="swiper-pagination"></div>
      </div>
      <div class="bao_type">
        <ul>
          <li class="list_wrap" v-for="(item,index) in list_type" :key="index">
            <div class="type-con">
              <span  class="type_info">{{item.title}}</span>
              <span class="check-more" @click="ckeckMore(item.id)">查看更多>></span>
            </div>
            <ul class="type_list">
              <li @click="todetail(item_list.id)" v-for="(item_list,index) in item.ins" :key="index">
                <div class="img_wrap">
                  <img :src="item_list.imgs" alt="">
                </div>
                <p class="danyi">{{item_list.title}}</p>
                <div class="person">已预约 <span>{{item_list.yu_num}}</span>人</div>
              </li>
            </ul>
          </li>
        </ul>
      </div>
      <a href="" class="kefu iconfont icon-kefu"></a>
    </div>
</template>

<script>
  import Swiper from 'swiper'
  import axios from 'axios'
import { apiGet } from '../common/js/api';
    export default {
        name: "Home",
      data(){
          return{
            list_type:
              [
                {
                  title:"保险分类一",
                  status:0,
                  list:[
                    {
                      "img": require('./img/timg.jpg'),
                      "title":"龙熙科技",
                      "id":'06',
                      "num":"999"
                    },
                    {
                      "img": require('./img/timg.jpg'),
                      "title":"龙熙科技",
                      "id":'07',
                      "num":"999"
                    },
                    {
                      "img": require('./img/timg.jpg'),
                      "title":"龙熙科技",
                      "id":'08',
                      "num":"999"
                    },
                  ]
                },
                {
                  title:"保险分类二",
                  status:1,
                  list:[
                    {
                      "img": require('./img/timg.jpg'),
                      "title":"龙熙科技",
                      "num":"999"
                    },
                    {
                      "img": require('./img/timg.jpg'),
                      "title":"龙熙科技",
                      "num":"999"
                    },
                    {
                      "img": require('./img/timg.jpg'),
                      "title":"龙熙科技",
                      "num":"999"
                    },
                  ]
                },
                {
                  title:"保险分类三",
                  status:2,
                  list:[
                    {
                      "img": require('./img/timg.jpg'),
                      "title":"龙熙科技",
                      "num":"999"
                    },
                    {
                      "img": require('./img/timg.jpg'),
                      "title":"龙熙科技",
                      "num":"999"
                    },
                    {
                      "img": require('./img/timg.jpg'),
                      "title":"龙熙科技",
                      "num":"999"
                    },
                  ]
                }
              ],
            banners:[],
            content: null
          }
      },
      created(){
        this.getInfo()
      },
      mounted(){
        
      },
      methods:{
        //获取首页数据
        getInfo(){
          apiGet('/insurance/index').then(res=>{
            console.log(res)
            this.list_type = res.data.data.insurances;
            this.banners = res.data.data.banner;
            console.log(this.banners)
            console.log(this.list_type)
            this.$nextTick(()=>{
              var mySwiper = new Swiper('.swiper-container', {
                autoplay: true,//可选选项，自动滑动
                loop : true,
                pagination: {
                  el: '.swiper-pagination',
                },
              })
            })
          })
        },
        //查看更多（搜索保險）
        ckeckMore(typeId){
          this.$router.push({
            path:'/insuranceSearch',
            query:{
              typeId
            }
          })
        },
        todetail(id){
          this.$router.push({
            path:'/xiangqin',
            query:{
              id,
            }
          })
        }
      }
    }
</script>

<style lang="scss">
  .swiper-pagination-bullet-active{
    width: 0.13rem !important;
    background: white !important;
    height: 0.06rem;
    border-radius: 0.03rem;
  }
  .swiper-container-horizontal>.swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction{
    bottom: 0.6rem;
  }
  .home{
    height: 100%;
    overflow: auto;
    background: #E6E9ED;
    .swiper-container{
      width: 100%;
      .swiper-slide>img{
        width: 100%;
        max-height: 2.56rem;
      }
    }
    .bao_type{
      width: 3.5rem;
      margin:0 auto 0;
      top: -0.48rem;
     padding-bottom: 0.3rem;
      background: #E6E9ED;
      position: relative;
      z-index: 2;
      min-height: 1.2rem;
      border-radius: 0.04rem;
      margin-bottom: -0.4rem;
      li{background: white;margin-bottom: 0.17rem;padding-top: 0.17rem;border-radius: 0.04rem}
      .type-con{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 0.31rem;
        width: 100%;
      }
      .type_info{
        width:1.12rem;
        height: 0.31rem;
        line-height: 0.31rem;
        border-radius:0px 16px 16px 0px;
        font-size: 0.17rem;
        text-align: center;
        color: white;
      }
      .check-more{
        color: #5D95F3;
        font-size: .13rem;
        margin-right: .06rem;
      }
      .type_list{
        width: 100%;
        overflow:auto;
        &::-webkit-scrollbar{display: none;};
        padding-left: 0.13rem;
        white-space: nowrap;
        li{
          width: 1.56rem;
          margin-top: 0.13rem;
          display: inline-block;
          margin-right: 0.13rem;
          .img_wrap{
            border:1px solid rgba(0,67,142,.1);
            width: 100%;
            height: 1.59rem;
            border-radius:4px;
            img{
              width: 100%;
              height: 100%;
              border-radius:4px;
            }
          }
          p{
            font-size: 0.16rem;
            font-weight: 600;
            line-height: 0.3rem;
            color: #333;
          }
          .person{
            color: #999999;
            font-size: 0.13rem;
            line-height: 0.28rem;
          }

        }
      }
      .list_wrap:nth-of-type(1n) .type_info{background:rgba(93,149,243,1);}
      .list_wrap:nth-of-type(2n) .type_info{background:#F1AA44;}
      .list_wrap:nth-of-type(3n) .type_info{background:#D95989;}
    }
    .kefu{
      width: 0.52rem;
      height: 0.52rem;
      background:linear-gradient(-90deg,rgba(70,133,238,1),rgba(155,193,255,1));
      border-radius:50%;
      box-shadow:0px 5px 10px 0px rgba(60,127,238,0.42);
      position: fixed;
      bottom: 1.5rem;
      display: block;
      text-align: center;
      line-height: 0.52rem;
      font-size: 0.28rem;
      color: white;
      z-index: 3;
      right: 0.11rem;
    }
  }
</style>



